/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-crosswinds-blocks-carousel-slide {

	.wp-block-group-placeholder__variations {
		list-style: none;
		display: flex;
		justify-content: center;
		flex-direction: row;
		flex-wrap: wrap;
		width: 100%;
		padding: 0;
		margin: 0;
	}
	.components-placeholder__instructions {
		text-align: center;
		margin-bottom: 18px;
	}
	.wp-block-group-placeholder__variations svg {
		fill: #ccc !important;
	}
	.wp-block-group-placeholder__variations svg:hover {
		fill: var(--wp-admin-theme-color) !important;
	}
	.wp-block-group-placeholder__variations > li {
		margin: 0 12px 12px 12px;
		width: auto;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.wp-block-group-placeholder__variations li > .wp-block-group-placeholder__variation-button {
		width: 44px;
		height: 32px;
		padding: 0;
		&:hover {
			box-shadow: none;
		}
	}
	.components-placeholder {
		min-height: auto;
		padding: 24px;
	}
	.is-small,
	.is-medium {
		.wp-block-group-placeholder__variations > li {
			margin: 12px;
		}
	}
}
